import { render } from 'react-dom';
import { Provider } from 'react-redux';
import { createRoot } from 'react-dom/client';
import configureStore from './configureStore';
import App from './App';

console.log(" index.js loaded");

const store = configureStore();
const container = document.getElementById('root');
const root = createRoot(container);

function startApp() {
  root.render(
    <Provider store={store}>
      <App />
    </Provider>
  );
}

// Only listen to wallet data if running inside the Nexus wallet
if (typeof NEXUS !== 'undefined') {
  import('nexus-module').then(({ listenToWalletData }) => {
    console.log(' Detected NEXUS. Starting wallet integration...');
    listenToWalletData(store);
    startApp();
  });
} else {
  console.warn(' NEXUS not detected. Starting in standalone mode.');
  startApp();
}
